import { OrbitControls, PerspectiveCamera, Environment, Float } from "@react-three/drei";
import { EffectComposer, HueSaturation, ChromaticAberration, GodRays, DepthOfField, BrightnessContrast } from "@react-three/postprocessing";
import { BlendFunction, Resizer, KernelSize } from "postprocessing";
import { Color, CylinderGeometry,SphereGeometry,  Mesh, MeshBasicMaterial } from "three";
import { Suspense } from "react";
import { FloatingIsland } from "./FloatingIsland";
import { Words } from "./Words";
import { SceneParticles } from "./SceneParticles";

let lightColor = new Color(1, 0.2, 0.1);
let mesh = new Mesh(
  // new CylinderGeometry(0.3, 0.3, 0.2, 20),
  new SphereGeometry(0.3, 20, 20),
  new MeshBasicMaterial({
    color: lightColor,
    transparent: true,
    opacity: 1,
  })
);

mesh.rotation.x = Math.PI * 0.5;
mesh.position.set(7.17, 10.7, -4.1);
mesh.scale.set(1.5, 1, 1);


export function SceneContainer() {
  return (
    <Suspense fallback={null}>
      <Environment background={"only"} files={process.env.PUBLIC_URL + "/textures/bg.hdr"} />
      <Environment background={false} files={process.env.PUBLIC_URL + "/textures/envmap.hdr"} />

      <PerspectiveCamera makeDefault fov={60} position={[10.75, 2.85, -15.35]} />
      <OrbitControls target={[1, 3, 0]} maxPolarAngle={Math.PI * 0.5}/>
      <Float
        speed={0.5} 
        rotationIntensity={0.6} 
        floatIntensity={0.6}
      >

        <primitive object={mesh} />
        <spotLight
          penumbra={1}
          distance={500}
          angle={60.65}
          attenuation={1}
          anglePower={3}
          intensity={0.3}
          color={lightColor}
          position={[1.19, 10.85, -4.45]}
          target-position={[0, 0, -1]}
        />


      <FloatingIsland/>
      <SceneParticles/>  
      <Words/>
      </Float>
      
      <EffectComposer stencilBuffer={false}>
        <DepthOfField
          focusDistance={0.011}
          focalLength={0.02}
          bokehScale={2}
        /> 
        <HueSaturation hue={0} saturation={-0.15} />
        <BrightnessContrast brightness={0.1} contrast={0.035} />
        <ChromaticAberration radialModulation={true} offset={[0.00175, 0.00175]} />
        <GodRays
          sun={mesh}
          blendFunction={BlendFunction.Screen}
          samples={40}
          density={0.95}
          decay={0.97}
          weight={0.6}
          exposure={0.3}
          clampMax={1}
          width={Resizer.AUTO_SIZE}
          height={Resizer.AUTO_SIZE}
          kernelSize={KernelSize.SMALL}
          blur={true}
        />
      </EffectComposer>
    </Suspense>
  );
}